<template>
  <section>
    <br/>
    <!--工具条-->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px">
      <el-form :inline="true" :model="filters" @submit.native.prevent>
        <el-form-item>
          <el-input
            v-model="filters.name"
            placeholder="名称"
            @keyup.enter.native="getModelExperimentList"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="getModelExperimentList">查询</el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleAdd" icon="el-icon-plus"
            >模型项目</el-button
          >
        </el-form-item>
      </el-form>
    </el-col>
    <el-table
      :data="modelexperiments"
      highlight-current-row
      v-loading="listLoading"
      :show-header="false"
      @selection-change="selsChange"
      style="width: 100%"
    >
      <el-table-column
        prop="name"
        label="模型项目名称"
        min-width="20%"
        sortable
        show-overflow-tooltip
      >
        <template slot-scope="scope">
          <el-card class="box-card">
            <div slot="header" class="clearfix">
              <router-link
                :to="{name: '模型实验跟踪', params: {experiment_id: scope.row.experiment_id}}"
                style="
                  cursor: pointer;
                  color: #0000ff;
                  text-decoration: underline;">
                {{ scope.row.name }}
              </router-link>
              <el-button style="float: right; padding: 3px 0" type="text"
                >私有</el-button
              >
            </div>
            <el-descriptions
              class="margin-top"
              title=""
              style="margin-top: 0px"
              :column="3"
              :size="size"
            >
              <el-descriptions-item
                label="lifecycle_stage"
                ><el-tag size="small" type="warning">{{scope.row.lifecycle_stage}}</el-tag></el-descriptions-item
              >
              <el-descriptions-item label="项目编号"
                ><el-tag size="small" type="info">{{scope.row.experiment_id}}</el-tag>
  
              </el-descriptions-item>
              <el-descriptions-item
                label="创建人"
                >暂无</el-descriptions-item>
            </el-descriptions>
            <div v-for="o in 1" :key="o" class="text item">
              {{'工件地址：' + scope.row.artifact_location }}
            </div>
          </el-card>
        </template>
      </el-table-column>
    </el-table>
    <!--工具条-->
    <el-col :span="24" class="toolbar">
      <el-pagination
        layout="prev, pager, next"
        @current-change="handleCurrentChange"
        :page-size="10"
        :page-count="total"
        style="float: right"
      >
      </el-pagination>
    </el-col>

    <!--新增界面-->
    <el-dialog
      title="创建模型项目"
      :visible.sync="addFormVisible"
      :close-on-click-modal="false"
      style="width: 75%; left: 12.5%"
    >
      <el-form
        :model="addForm"
        label-width="80px"
        :rules="addFormRules"
        ref="addForm"
      >
        <el-form-item label="模型项目" prop="name">
          <el-input v-model="addForm.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="工件路径" prop="artifact_location">
          <el-input v-model="addForm.artifact_location" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="标签" prop="tag">
          <el-input
            type="textarea"
            :rows="2"
            v-model="addForm.tag"
          ></el-input>
          <span
            >创建后，您可以将模型参数和工件注册到该实验下。<el-link
              href="https://www.zhihu.com/people/leepand"
              target="_blank"
              type="primary"
              >了解更多。</el-link
            ></span
          >
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="addFormVisible = false">取消</el-button>
        <el-button
          type="primary"
          @click.native="addSubmit"
          :loading="addLoading"
          >提交</el-button
        >
      </div>
    </el-dialog>
  </section>
</template>

<script>
// import NProgress from 'nprogress'
import $ from 'jquery'
import moment from 'moment'
import { test, getModelExperiments, delModelExperiment, addModelExperiment } from '@/api/api'
// import ElRow from "element-ui/packages/row/src/row";
export default {
  // components: {ElRow},
  data () {
    return {
        forked:-1,
      users: {},
      filters: {
        name: ''
      },
      modelexperiments: [],
      total: 0,
      page: 1,
      listLoading: false,
      sels: [], // 列表选中列
      addFormVisible: false, // 新增界面是否显示
      addLoading: false,
      addFormRules: {
        name: [
          { required: true, message: '请输入名称', trigger: 'blur' },
          { min: 1, max: 50, message: '长度在 1 到 50 个字符', trigger: 'blur' }
        ],
        description: [
          { required: false, message: '请输入版本号', trigger: 'blur' },
          { max: 1024, message: '不能超过1024个字符', trigger: 'blur' }
        ]
      },
      // 新增界面数据
      addForm: {
        name: '',
        description: ''
      }
    }
  },
  methods: {
      handleFork(id){
        this.forked=id
      },
    // row[column.property] 能读取到该行该列的数据。代码中实现了时间格式的转换
    formatTime2 (row, column) {
      const date = new Date(row[column.property])
      return date.getFullYear() + '-' +
                    date.getMonth() + '-' +
                    date.getDate() + ' ' +
                    date.getHours() + ':' +
                    date.getMinutes() + ':' +
                    date.getSeconds()
    },
    formatTime: function (row, column) { // 日期格式化
      var date = row[column.property]
      if (typeof date === 'undefined') {
        return ''
      }
      return moment(date).format('YYYY-MM-DD HH:mm:ss')
    },

    getUserInfo () {
      let self = this

      let param = { experiment_id: self.$route.params.experiment_id }
      $.ajax({
        type: 'get',
        url: test + '/api/user/get_user_meta',
        async: true,
        data: param,
        headers: {
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        },
        timeout: 5000,
        success: function (data) {
          if (data.code === '999999') {
            self.users = data.data.users
            console.log(self.users, 'self.users')
          } else {
            self.$message.error({
              message: data.msg,
              center: true
            })
          }
        }
      })
    },
    // 获取模型项目列表
    getModelExperimentList () {
      this.listLoading = true
      let self = this
      let params = { page: self.page,
        user_key: JSON.parse(sessionStorage.getItem('name')),
        name: self.filters.name }
      // let headers = {Authorization: 'Token '+JSON.parse(sessionStorage.getItem('token'))};
      let headers = {
        'Content-Type': 'application/json',
        Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
      }
      getModelExperiments(headers, params).then((res) => {
        self.listLoading = false
        let { msg, code, data } = res
        if (code === '999999') {
          self.total = data.total
          self.modelexperiments = data.data["experiments"]
            console.log("self.modelexperiments",self.modelexperiments,typeof self.modelexperiments)
        } else {
          self.$message.error({
            message: msg,
            center: true
          })
        }
      })
    },
    handleCurrentChange (val) {
      this.page = val
      this.getModelExperimentList()
    },
    // 显示新增界面
    handleAdd: function () {
      this.addFormVisible = true
    },
    // 新增
    addSubmit: function () {
      this.$refs.addForm.validate((valid) => {
        if (valid) {
          let self = this
          this.$confirm('确认提交吗？', '提示', {}).then(() => {
            self.addLoading = true
            // NProgress.start();
            let params = JSON.stringify({
              name: self.addForm.name,
              tag: self.addForm.tag,
              artifact_location: self.addForm.artifact_location,
              user: JSON.parse(sessionStorage.getItem('name'))
            })
            let header = {
              'Content-Type': 'application/json',
              Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
            }
            addModelExperiment(header, params).then(_data => {
              let { msg, code } = _data
              self.addLoading = false
              if (code === '999999') {
                self.$message({
                  message: '模型项目添加成功',
                  center: true,
                  type: 'success'
                })
                self.$refs['addForm'].resetFields()
                self.addFormVisible = false
                self.getModelExperimentList()
              } else if (code === '999997') {
                self.$message.error({
                  message: msg,
                  center: true
                })
              } else {
                self.$message.error({
                  message: msg,
                  center: true
                })
                self.$refs['addForm'].resetFields()
                self.addFormVisible = false
                self.getModelExperimentList()
              }
            })
          })
        }
      })
    },
    // 批量删除
    batchRemove: function () {
      let names = this.sels.map(item => item.name)
      this.$confirm('确认删除选中模型吗？', '提示', {
        type: 'warning'
      }).then(() => {
        this.listLoading = true
        // NProgress.start();
        let self = this
        let params = { names: names, user_key: JSON.parse(sessionStorage.getItem('name')) }
        let header = {
          'Content-Type': 'application/json',
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        }
        delModelExperiment(header, params).then(_data => {
          let { msg, code } = _data
          if (code === '999999') {
            self.$message({
              message: '模型批量删除成功',
              center: true,
              type: 'success'
            })
          } else {
            self.$message.error({
              message: msg,
              center: true
            })
          }
          self.getModelExperimentList()
        })
      })
    },
    // 删除
    handleDel: function (index, row) {
      this.$confirm('确认删除该模型吗?', '提示', {
        type: 'warning'
      }).then(() => {
        this.listLoading = true
        // NProgress.start();
        let self = this
        let params = { names: [row.name],
          user_key: JSON.parse(sessionStorage.getItem('name')) }
        let header = {
          'Content-Type': 'application/json',
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        }
        delModelExperiment(header, params).then(_data => {
          let { msg, code } = _data
          if (code === '999999') {
            self.$message({
              message: '删除成功',
              center: true,
              type: 'success'
            })
          } else {
            self.$message.error({
              message: msg,
              center: true
            })
          }
          self.getModelExperimentList()
        })
      })
    },
    selsChange: function (sels) {
      this.sels = sels
    }
  },
  mounted () {
    this.getModelExperimentList()
    //this.getUserInfo()
  }
}
</script>

<style></style>
